/* Размеры вьюпорта */
/* Для ретинового медиавыражения */
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-light.woff2") format("woff2"), url("../fonts/opensans-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-semibold.woff2") format("woff2"), url("../fonts/opensans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  line-height: 1.48;
  color: #373737;
  font-size: 14px;
  font-weight: 300;
  font-family: "Open Sans", "Helvetica", "Arial", sans-serif;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden,
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  margin: 0 auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    width: 668px;
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1100px;
  }
}
.header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
  position: relative;
}
.header--donate::before:hover {
  top: 180px;
}
.header--donate .header__icon-cat {
  animation: donate 3s;
}
.header__logo {
  position: relative;
}
.header__logo::before,
.header__logo::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #008000;
  opacity: 0;
}
.header__logo::before {
  top: -100%;
}
.header__logo::after {
  bottom: -100%;
}
.header__logo:hover::before {
  animation: logotop 2s ease-in-out;
}
.header__logo:hover::after {
  animation: logobot 2s ease-in-out;
}
.header__icon-cat {
  display: none;
  position: absolute;
  top: 160px;
  left: 50%;
  z-index: 10;
  margin: 0 0 0 -130px;
}
.header__icon-cat img {
  transition: all 0.3s ease-in-out;
}
.header__icon-cat:hover img {
  transform: translateY(50px);
}
.header__logo-image {
  width: 150px;
}
@media (min-width: 768px) {
  .header__icon-cat {
    display: block;
  }
  .header__logo-image {
    display: block;
    width: 200px;
  }
  .header {
    justify-content: space-between;
  }
}
@keyframes donate {
  0%,
  100% {
    transform: translateY(0);
  }
  50%,
  60%,
  70% {
    transform: translateY(-70px);
  }
}
@keyframes logotop {
  0%,
  100% {
    top: -100%;
    opacity: 0;
  }
  50%,
  60%,
  70% {
    top: 100%;
    opacity: 1;
  }
}
@keyframes logobot {
  0%,
  100% {
    bottom: -100%;
    opacity: 0;
  }
  50%,
  60%,
  70% {
    bottom: 100%;
    opacity: 1;
  }
}
.nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin: 0 0 0 40px;
}
.nav__item {
  margin: 0 0 10px;
}
.nav__link {
  display: block;
  font-size: 16px;
  font-weight: 600;
  color: #373737;
  transition: all 0.3s ease-in-out;
}
.nav__link--donate {
  display: none;
}
.nav__link:hover {
  transform: translateY(-5px);
  text-shadow: 0 15px rgba(0, 0, 0, 0.4);
}
.nav__link:active {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .nav__list {
    display: flex;
    margin: 0;
  }
  .nav__item {
    margin: 0;
  }
  .nav__link {
    margin-left: 20px;
    font-size: 20px;
  }
  .nav__link--donate {
    display: block;
  }
}
@media (min-width: 1200px) {
  .nav__link {
    margin-left: 40px;
  }
}
.team {
  background: url("../img/bg-zigzag.svg") repeat-x center bottom;
  background-size: 800px auto;
}
.jumbotron {
  position: relative;
  z-index: 20;
  margin: 0 auto 100px;
  padding: 0 30px;
  background: #0a0933 url("../img/bg-head-mobile@1x.jpg") no-repeat center top;
  background-size: 768px auto;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .jumbotron {
    background-image: url("../img/bg-head-mobile@2x.jpg");
  }
}
.jumbotron__title {
  margin: 0;
  padding: 80px 0;
  color: #ffffff;
  font-size: 30px;
  font-weight: 700;
  text-align: center;
}
@media (min-width: 768px) {
  .jumbotron {
    width: 760px;
    background-image: url("../img/bg-head-tablet@1x.jpg");
    background-position: center top;
    background-size: 1200px auto;
  }
  .jumbotron__title {
    font-size: 50px;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .jumbotron {
    background-image: url("../img/bg-head-tablet@2x");
  }
}
@media (min-width: 1200px) {
  .jumbotron {
    width: 1180px;
    background-image: url("../img/bg-head-desktop@1x.jpg");
  }
  .jumbotron__title {
    font-size: 70px;
  }
}
@media (min-width: 1200px) and (min-resolution: 144dpi), (min-width: 1200px) and (min-resolution: 1.5dppx) {
  .jumbotron {
    background-image: url("../img/bg-head-desktop@2x.jpg");
  }
}
.team {
  padding-bottom: 30px;
}
.team__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.team__item {
  width: 260px;
  margin: 0 auto 70px;
}
.team__image {
  display: block;
  width: 100%;
  margin: 0 auto;
  border-radius: 100%;
  box-shadow: 0 0 0 8px #ffffff, 0 0 0 16px #0a0933;
  transition: all 0.5s ease-in-out;
}
.team__image:hover {
  transform: rotate(160deg);
}
.team__name {
  margin: 35px 0 10px;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
}
.team__name span {
  color: #0a0933;
  font-weight: 600;
}
.team__text {
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .team__image {
    width: 200px;
  }
  .team__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .team__item {
    margin: 0 30px 70px;
  }
}
@media (min-width: 1200px) {
  .team__image {
    width: 230px;
  }
  .team__list {
    justify-content: space-between;
  }
  .team__item {
    width: 250px;
    margin: 0 0 70px;
  }
}
.footer {
  padding: 70px 30px 50px;
  text-align: center;
}
.footer__copyright {
  margin: 30px 0 0;
  font-weight: 600;
}
@media (min-width: 768px) {
  .footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    text-align: left;
  }
  .footer__copyright {
    margin: 0;
  }
}
.social__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}
.social__item:not(:last-child) {
  margin: 0 15px 0 0;
}
.social__title {
  margin: 0 0 15px;
  font-weight: 600;
}
.social__link {
  display: block;
  border-radius: 6px;
  transition: all 0.3s ease-in-out;
}
.social__link svg {
  border-radius: 8px;
}
.social__link:hover {
  background-color: #cecece;
}
.social__link:active {
  opacity: 0.5;
}
@media (min-width: 768px) {
  .social__list {
    justify-content: flex-start;
  }
}
